
import { defineComponent } from "vue";
import { Waypoint } from "vue-waypoint";
import HomeView from "@/views/HomeView.vue";
import AboutView from "@/views/AboutView.vue";
import EnEView from "@/views/EnEView.vue";
import SkillView from "@/views/SkillView.vue";
import WorkView from "@/views/WorkView.vue";

export default defineComponent({
  components: {
    HomeView,
    AboutView,
    Waypoint,
    EnEView,
    SkillView,
    WorkView,
  },
  data() {
    return {
      isTop: true,
      navLists: [
        {
          name: "Home",
          path: "#home",
        },
        {
          name: "About",
          path: "#about",
        },
        {
          name: "Experience_&_Education",
          path: "#experienceAndEdu",
        },
        {
          name: "Skill",
          path: "#skill",
        },
        {
          name: "Work",
          path: "#work",
        },
        {
          name: "Download_My_CV",
          path: "https://drive.google.com/uc?export=download&id=1icZVrE2vIzNF73tuznT6Rk7nlUQhGg59",
        },
      ],
    };
  },
  methods: {
    onchange(state: any) {
      console.log(state.going);
      if (state.going == "IN") {
        this.isTop = true;
      } else {
        this.isTop = false;
      }
    },
    setLang() {
      // let currentLang = this.$store.getters.currentLang;
      let currentLang = localStorage.getItem("set-lang");
      // this.$store.commit("SET_LANG", currentLang == "tw" ? "en" : "tw");
      localStorage.setItem("set-lang", currentLang == "tw" ? "en" : "tw");
      this.$router.go(0);
    },
  },
});
